<template>
    <div>
      <a-descriptions title="数据标注" style="width: 80%;margin-left: 200px;">
        <a-descriptions-item label="点击选择">

          <a-cascader :options="options" placeholder="Please select" @change="onChange" class="custom-cascader" />

          </a-descriptions-item>
          <a-descriptions-item label="日期">{{date_name }}</a-descriptions-item>
          <a-descriptions-item label="时刻">{{people_name }}</a-descriptions-item>
          
          <a-descriptions-item label="当前展示类">{{ plane_name }}</a-descriptions-item>
          <a-descriptions-item label="点击修改类名称">
            <a-select
      ref="select"
      v-model:value="plane_class"
      style="width: 120px"
      @focus="focus"
      @change="handleChange"
    >
      <a-select-option value="1">1</a-select-option>
      <a-select-option value="2">2</a-select-option>
      <a-select-option value="3" >3</a-select-option>
      <a-select-option value="4">4</a-select-option>
      <a-select-option value="5">5</a-select-option>
      <a-select-option value="6">6</a-select-option>
      <a-select-option value="7" >7</a-select-option>
      <a-select-option value="8">8</a-select-option>
      <a-select-option value="9" >9</a-select-option>
      <a-select-option value="10">10</a-select-option>
    </a-select>
      <a-button @click="changePlaneName">确认修改</a-button>
      </a-descriptions-item>
      <a-descriptions-item label="点击删除类">
        <a-button @click="deletePlane">删除类</a-button>
      </a-descriptions-item>
       
        </a-descriptions>
        
      
    </div>
    <div>
      <ImageShow :key="imageShowKey"></ImageShow>
    </div>
</template>

<script setup>
import ImageShow from './ImageShow.vue';
import axios from 'axios';
import { ref , onMounted} from 'vue';

// 定义响应式变量
// 用于强制重新渲染 ImageShow 组件的 key
const imageShowKey = ref(0);
const plane_class = ref(1);
const exam_path = ref('D:/desktop_fake/imgs_classify_ceshi');
const date_name = ref('2024-05-09');
const people_name = ref('2024-05-09_07-46-36');
const plane_name = ref('02_');
const options = ref([
  {
    value: 'zhejiang',
    label: 'Zhejiang',
    children: [
      {
        value: 'hangzhou',
        label: 'Hangzhou',
        children: [
          {
            value: 'xihu',
            label: 'West Lake',
          },
        ],
      },
    ],
  },
  {
    value: 'jiangsu',
    label: 'Jiangsu',
    children: [
      {
        value: 'nanjing',
        label: 'Nanjing',
        children: [
          {
            value: 'zhonghuamen',
            label: 'Zhong Hua Men',
          },
        ],
      },
    ],
  },
]);

// 修改页面名称
const changePlaneName = async ()=>{
  const old_name = plane_name.value
  const new_class = plane_class.value
  console.log("img name:", old_name, " new class:", new_class)
  try {
    const url = '/api/changePlaneName';
    const formData = new FormData();
    formData.append('exam_path', exam_path.value);
    formData.append('date_name',date_name.value);
    formData.append('people_name', people_name.value);
    formData.append('plane_name',plane_name.value);
    formData.append('new_class',plane_class.value);
    const response = await axios.post(url, formData);
    if (response.data.status == 200){
      plane_name.value = response.data.message;
      await getAllInfo();
    }
    console.log(response.data)
  } catch (error) {
    console.error(error);
  }
};
//删除类
const deletePlane = async ()=>{
  const old_name = plane_name.value
  console.log("delete:", old_name)
  try {
    const url = '/api/deletePlane';
    const formData = new FormData();
    formData.append('exam_path', exam_path.value);
    formData.append('date_name',date_name.value);
    formData.append('people_name', people_name.value);
    formData.append('plane_name',plane_name.value);
    const response = await axios.post(url, formData);
    if (response.data.status == 200){
      plane_name.value = response.data.message;
      await getAllInfo();
    }
    console.log(response.data)
  } catch (error) {
    console.error(error);
  }
};
// 页面加载时获取数据
onMounted(async () => {
  await getAllInfo();
  localStorage.setItem('exam_path', exam_path.value);
  localStorage.setItem('date_name', date_name.value);
  localStorage.setItem('people_name', people_name.value);
  localStorage.setItem('plane_name', plane_name.value);
});

// 获取所有信息
const getAllInfo = async () => {
  try {
    const url = '/api/getAllInfo';
    const formData = new FormData();
    formData.append('exam_path', exam_path.value);

    const response = await axios.post(url, formData);
    options.value = response.data.message;
  } catch (error) {
    console.error(error);
  }
};

// 下拉选项变化时触发的函数
const onChange = (value) => {
  console.log("选中：" + value);
  date_name.value = value[0];
  people_name.value = value[1];
  plane_name.value = value[2];
  // 将值存储到 localStorage
  localStorage.setItem('date_name', date_name.value);
  localStorage.setItem('people_name', people_name.value);
  localStorage.setItem('plane_name', plane_name.value);
  imageShowKey.value++;
};
</script>
<style scoped>
.custom-cascader {
  /* margin-top: 0px; */
  width: 300px; 
}
</style>